
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Panel from 'vux/src/components/panel/index.vue'

import { SET_BACK } from '@/store/types'
export default {
name: 'index',
data() {
return {
list: []
}
},
methods: {
itemClick(item) {
console.log(item)
this.$router.push({ path: `/detail/${item.id}` })
},
async getData() {
try {
const res = await this.$http.get(`static/data/news.json`)
this.list = res.map(c => {
return {
id: c.id,
src: c.image,
title: c.title,
desc: c.desc
}
})
} catch(e) {
console.log(e)
}
}
},
created() {
this.$store.commit(SET_BACK, false)
this.getData()
},
components: {
Swiper,
SwiperItem,
Icon,
Grid,
GridItem,
Panel
}
}



